import React, { useEffect, useState } from 'react';
import Text from '../Text/Text';
import { RightSpaced, TopSpaced } from '../Spaced/Spaced';
import addInputPhoneWallet, { showPhoneError, validatePhone, } from '../../features/optin/phoneNumber';
import Button from '../Button/Button';
import AppLogo from '../AppCommons/AppLogo';
import AppBottomBorder from '../AppCommons/AppBottomBorder';
import SuccessPagePanel from '../Panel/SuccessPagePanel';
import useUser from '../../hooks/useUser';
import SuccessPanel from '../Panel/SuccessPanel';
import useMakeRequest from '../../hooks/useMakeRequest';
import useLogWhenRendering from '../../hooks/useLogWhenRendering';
import useAppProps from '../../hooks/useAppProps';
const PhonePanel = ({ title, subtitle, buttonTitle, logTag = 'PhonePanel', ...props }) => {
    useLogWhenRendering(`[${logTag}] phone panel rendered`);
    const { eventService } = useAppProps();
    const user = useUser();
    const [userUpdated, setUserUpdated] = useState(false);
    const onErrorUpdatingPhone = (errorMessage) => {
        showPhoneError(errorMessage);
        eventService.registerInLoggly(`[${logTag}] max phone reached`);
    };
    const { makeRequest: updateUserPhone, loading } = useMakeRequest(async (api, phone) => {
        await api.updateUser({ phone });
        setUserUpdated(true);
        eventService.registerInLoggly(`[${logTag}] phone submitted`);
    }, onErrorUpdatingPhone);
    useEffect(() => {
        addInputPhoneWallet($('#phoneInput'), {
            showLabel: false,
            validateOnBlur: false,
            onPhoneValid: async (newPhone) => {
                await updateUserPhone(newPhone);
            },
            initialPhone: user.phone,
        });
    }, []);
    return userUpdated ? (<SuccessPanel title="Prontinho, celular salvo!" subtitle="Nas suas prximas compras, voc receber o cdigo de segurana por SMS."/>) : (<SuccessPagePanel data-testid={props['data-testid']}>
      <Text type="title">{title}</Text>
      <TopSpaced>
        <Text type="small">{subtitle}</Text>
      </TopSpaced>
      <TopSpaced id="phoneInput" size="large"/>
      <TopSpaced size="large">
        <Button data-testid="submitPhone" onClick={validatePhone} loading={loading}>
          <RightSpaced>{buttonTitle}</RightSpaced>
          <AppLogo />
        </Button>
      </TopSpaced>
      <AppBottomBorder />
    </SuccessPagePanel>);
};
export default PhonePanel;
