import TimeUtils from './time.utils';
import NumberUtils from './number.utils';
class Timer {
    constructor(config) {
        this.DEFAULT_STEP_SECONDS = 1;
        this.interval = null;
        this.start = () => {
            if (this.reachedZero()) {
                this.config.onTimerFinished();
            }
            else {
                this.interval = setInterval(() => {
                    this.advanceTime();
                    if (this.reachedZero()) {
                        this.config.onTimerFinished();
                        this.stop();
                    }
                    if (this.config.step) {
                        this.config.step(this.config.minutes, this.config.seconds);
                    }
                }, TimeUtils.secondsToMilliseconds(this.config.stepTime));
            }
        };
        this.stop = () => {
            if (this.interval)
                clearInterval(this.interval);
        };
        this.current = () => ({
            seconds: this.config.seconds,
            minutes: this.config.minutes,
        });
        this.reachedZero = () => this.config.minutes === 0 && NumberUtils.round(this.config.seconds) === 0;
        this.advanceTime = () => {
            this.config.seconds -= this.config.stepTime;
            if (this.config.seconds === 0 && this.config.minutes > 0) {
                this.config.minutes -= 1;
                this.config.seconds = 59;
            }
        };
        this.config = {
            ...config,
            minutes: config.minutes || 0,
            stepTime: config.stepTime || this.DEFAULT_STEP_SECONDS,
        };
    }
}
Timer.startWith = (config) => {
    const timer = new Timer(config);
    timer.start();
    return timer;
};
export default Timer;
