import OtpEmailState from './otpEmailState';
import { InvisibleOptInOTPDescription } from '../../../../resources/html/otp.html';
class OtpInvisibleOptInState extends OtpEmailState {
    constructor() {
        super(...arguments);
        this.shouldShowTermsAndConditions = true;
        this.applyDescription = () => {
            $('#OTPDescriptionContainer')
                .empty()
                .append($(InvisibleOptInOTPDescription));
        };
    }
}
export default OtpInvisibleOptInState;
