import Storefront from '../../storefront';
import Checkout from '../../checkout';
import { setCrossStoreImpossible } from '../../../utils/cookie.utils';
import MessageHandler from '../../messageHandler';
import WalletAuthProxy from '../walletAuthProxy';
class ReadyMessageHandler extends MessageHandler {
    constructor(appProps) {
        super();
        this.handleMessage = (payload) => {
            console.log('[wallet-auth-proxy] WalletAuthProxy: ReadyMessageHandler');
            if (!payload) {
                return;
            }
            setCrossStoreImpossible(payload.isCrossStoreImpossible);
            if (payload.isCrossStoreImpossible) {
                window.location.reload();
                return;
            }
            WalletAuthProxy.postMessage(WalletAuthProxy.messages.INITIALIZE, {
                cartId: Storefront.getCartId(),
                cartHash: Storefront.getCartHash(),
                storeId: Storefront.getStoreId(),
                email: Checkout.getEmail(),
            });
            WalletAuthProxy.postMessage(WalletAuthProxy.messages.LOGGED_USER);
        };
        this.shouldHandle = (message) => message === WalletAuthProxy.messages.READY;
        this.appProps = appProps;
    }
}
export default ReadyMessageHandler;
