import WalletIframe from '../../../services/walletIframe/walletIframe';
import OtpAfterEmailState from './states/otpAfterEmailState';
import OtpAfterSmsState from './states/otpAfterSmsState';
import Timer from '../../../utils/timer.utils';
import OTPConfig from './config';
import OtpAlwaysEmailState from './states/otpAlwaysEmailState';
class OTPResend {
    constructor(webOTP, otpFlow) {
        this.webOTP = webOTP;
        this.otpFlow = otpFlow;
        this.subscribeToOnResend = (subscriber) => {
            this.subscribers.push(subscriber);
        };
        this.applyResendViaEmail = (data) => {
            $('#resendViaEmail').on('click', () => {
                this.setupResendViaEmailState(data);
            });
        };
        this.applyMixedResend = (data) => {
            $('#resendViaSMS').on('click', () => {
                this.setupResendViaSmsState(data);
            });
            $('#resendViaEmail').on('click', () => {
                this.setupResendViaEmailState(data);
            });
        };
        this.applyGenericResendViaEmail = (data) => {
            $('#genericResend').on('click', () => {
                this.setupAlwaysResendViaEmail(data);
            });
        };
        this.setupResendViaEmailState = (data) => {
            this.setupState(new OtpAfterEmailState(data, this.webOTP, this));
        };
        this.setupResendViaSmsState = (data) => {
            this.setupState(new OtpAfterSmsState(data, this.webOTP, this));
        };
        this.setupAlwaysResendViaEmail = (data) => {
            this.setupState(new OtpAlwaysEmailState(data, this.webOTP, this));
        };
        this.setupState = (newState) => {
            newState.handle();
            this.otpFlow.updateState(newState);
            this.subscribers.forEach((subscriber) => subscriber());
            WalletIframe.postMessage(WalletIframe.messages.CHANGE_OTP_CHANNEL, {
                channel: this.webOTP.getCurrentChannel(),
            });
            $('#codeSent').fadeIn();
            Timer.startWith({
                seconds: OTPConfig.CODE_SENT_WAIT_SECONDS,
                onTimerFinished: () => {
                    const codeSent = $('#codeSent');
                    if (codeSent.length) {
                        codeSent.fadeOut(() => {
                            newState.startTimer();
                        });
                    }
                    else {
                        newState.startTimer();
                    }
                },
            });
        };
        this.subscribers = [];
    }
}
export default OTPResend;
