class OrderService {
    constructor(props, data) {
        this.props = props;
        this.data = data;
        this.buildOrderData = () => ({
            ...this.data,
            createdIn: this.props.loginService.isInvisibleOptInLogin()
                ? this.props.loginService.getLoginType()
                : this.data.createdIn,
        });
    }
    create() {
        return this.props.api.createOrder(this.buildOrderData());
    }
}
export default OrderService;
