import { Logger } from '../../services/logger';
import { removeCookiesAuth } from '../../utils/cookie.utils';
import ErrorHandler from '../../services/errorHandler';
import boxLogoutNuvemPayActionHTML from '../../resources/html/logout/logoutNuvemPayAction.html';
import boxTextLogoutNuvemPayHTML from '../../resources/html/logout/textLogoutNuvemPay.html';
const clearLogout = () => {
    $('#logoutNuvemPayAction').remove();
    $('#textLogoutNuvemPay').remove();
};
// eslint-disable-next-line import/prefer-default-export
export const renderLogout = (loginService) => {
    clearLogout();
    $('#reviewBlockEnvelope').append(boxLogoutNuvemPayActionHTML());
    $('#reviewBlockContentEmail').append(boxTextLogoutNuvemPayHTML());
    $('#logoutNuvemPayAction').on('click', async () => {
        try {
            Logger('Click to logout');
            await loginService.logout();
            clearLogout();
            removeCookiesAuth();
            $('.breadcrumb-step.visited').trigger('click');
        }
        catch (error) {
            ErrorHandler.reportException(error);
        }
    });
};
