import InvisibleOptIn from '../../optin/invisibleOptIn';
import OTPModalOpener from './modalOpener';
class InvisibleOptInModalOpener extends OTPModalOpener {
    constructor() {
        super(...arguments);
        this.containerId = 'invisibleOptInContainer';
        this.buttonId = 'invisibleOptInButton';
        this.show = (appProps, onClick) => {
            new InvisibleOptIn(appProps.eventService).render();
            $(`#${this.buttonId}`).on('click', onClick);
        };
    }
}
export default InvisibleOptInModalOpener;
