import { setSameStore, setCrossStore } from '../../../utils/cookie.utils';
import Storefront from '../../storefront';
import WalletAuthProxy from '../walletAuthProxy';
import MessageHandler from '../../messageHandler';
import { convertCamelToSnake } from '../../../utils/string.utils';
class LoggedUserHandler extends MessageHandler {
    constructor(appProps) {
        super();
        this.handleMessage = (payload) => {
            console.log('[wallet-auth-proxy] WalletAuthProxy: LoggedUserHandler');
            const { user, sameStore } = payload;
            this.determineSameOrCrossStore(sameStore);
            if (user) {
                this.verifyRC(user);
            }
        };
        this.determineSameOrCrossStore = (sameStoreFlag) => {
            if (sameStoreFlag)
                setSameStore();
            if (!sameStoreFlag)
                setCrossStore();
        };
        this.verifyRC = (user) => {
            const urlParams = new URLSearchParams(window.location.search);
            const returningCustomer = urlParams.get('rc');
            const verifyRC = returningCustomer === '1';
            if (!Storefront.getStoreFrontAddress().zipcode || !verifyRC) {
                const camelCaseUserObj = convertCamelToSnake(user);
                this.appProps.loginService.login({
                    user: camelCaseUserObj,
                });
            }
        };
        this.shouldHandle = (message) => message === WalletAuthProxy.messages.LOGGED_USER;
        this.appProps = appProps;
    }
}
export default LoggedUserHandler;
