import RedirectHandler from './redirectHandler';
import Config from '../../utils/config.utils';
import Checkout, { CheckoutEvents } from '../checkout';
class StartPageRenderer extends RedirectHandler {
    handleRedirectToStart() {
        if (Config.ENABLE_LOGIN) {
            Checkout.publishEvent(CheckoutEvents.BLOCK_VALIDATE_EMAIL, true);
            this.appProps.emailInputHandler.attachTo($("input[id='contact.email']"));
        }
    }
}
export default StartPageRenderer;
