import Config from '../utils/config.utils';
export const enableConsoleLogging = Config.ENABLE_CONSOLE_LOGGING;
export const Logger = (title, content = {}) => {
    const payloadContent = typeof content === 'object' && !Array.isArray(content)
        ? content
        : { data: content };
    if (enableConsoleLogging) {
        // eslint-disable-next-line no-console
        console.log('Wallet log:', { title, payloadContent });
    }
    $.ajax({
        dataType: 'json',
        method: 'POST',
        contentType: 'application/json; charset=utf-8',
        url: `${Config.BASE_URL_CHECKOUT_API}/checkout/v3/logger`,
        data: JSON.stringify({
            payload: {
                0: `[Wallet] ${title}`,
                ...payloadContent,
            },
        }),
    });
};
