import React from 'react';
import styles from './styles.css';
import LoadingSpinner from '../Loading/LoadingSpinner';
const KEYS_THAT_TRIGGER_CLICK = ['Enter'];
const Button = ({ children, type = 'primary', onClick, loading = false, className, id, ...props }) => {
    const handleKeyDown = (event) => {
        if (KEYS_THAT_TRIGGER_CLICK.includes(event.key) && onClick) {
            onClick();
        }
    };
    return (<div id={id} role="button" tabIndex={0} className={`${styles[type]} ${className}`} data-testid={props['data-testid']} onClick={onClick} onKeyDown={handleKeyDown}>
      {loading ? <LoadingSpinner /> : children}
    </div>);
};
export default Button;
