import { getCookieByKeyName, NameCookiesUsed } from '../../utils/cookie.utils';
import boxAcceptCookieLogin from './boxAcceptCookieLogin';
class AcceptCookieLogin {
    constructor(emailInputHandler) {
        this.render = () => {
            const emailCookie = getCookieByKeyName(NameCookiesUsed.CHECKOUT_SAVE_COOKIE_EMAIL);
            const inputEmail = $("input[id='contact.email']");
            if (emailCookie && !inputEmail.val()) {
                boxAcceptCookieLogin(emailCookie, inputEmail, this.emailInputHandler);
            }
        };
        this.emailInputHandler = emailInputHandler;
    }
}
export default AcceptCookieLogin;
