class Stopwatch {
    constructor() {
        this.startTime = 0;
        this.start = () => {
            this.startTime = this.currentTime();
        };
        this.secondsSinceLastStart = () => Stopwatch.millisecondsToSeconds(this.currentTime() - this.startTime);
        this.currentTime = () => new Date().getTime();
    }
}
Stopwatch.millisecondsToSeconds = (milliseconds) => milliseconds / 1000;
export default Stopwatch;
