import { createRoot } from 'react-dom/client';
import wrapInContext from '../components/AppCommons/AppContext';
const addReactComponentToRoot = (component, rootId, appProps) => {
    const rootElement = document.getElementById(rootId);
    const root = createRoot(rootElement);
    root.render(wrapInContext(component, appProps));
};
export default addReactComponentToRoot;
