import { resendViaEmailOrSmsButton } from '../../../../resources/html/otp.html';
import OTPConfig from '../config';
import OtpSmsState from './otpSmsState';
/* State after sending via sms */
class OtpAfterSmsState extends OtpSmsState {
    constructor() {
        super(...arguments);
        this.resendButton = resendViaEmailOrSmsButton;
        this.getTimerConfig = () => ({
            seconds: OTPConfig.LONG_RESEND_WAIT_SECONDS,
        });
        this.setupResend = () => {
            this.otpResend.applyMixedResend(this.data);
        };
    }
}
export default OtpAfterSmsState;
