import init from './wallet';
import ErrorHandler from './services/errorHandler';
import Config from './utils/config.utils';
import Storefront from './services/storefront';
if (!window.loadedWallet &&
    (Config.ENABLE_WALLET || Storefront.overridesWalletEnabledFlag())) {
    ErrorHandler.init();
    try {
        init();
    }
    catch (error) {
        ErrorHandler.reportException(error);
    }
}
window.loadedWallet = true;
