import React from 'react';
import classNames from 'classnames';
import styles from './styles.css';
import AppBottomBorder from '../AppCommons/AppBottomBorder';
import Button from '../Button/Button';
import { useDesktopMediaQuery } from '../Responsive/useMediaQuery';
const BubbleDialog = ({ children, onClick, id }) => {
    const isDesktop = useDesktopMediaQuery();
    return (<Button type="container" className={classNames(styles.container, styles.bubble, isDesktop && styles.desktopContainer)} onClick={onClick} id={id}>
      {children}
      <AppBottomBorder />
    </Button>);
};
export default BubbleDialog;
