import React, { useState } from 'react';
import SuccessPagePanel from './SuccessPagePanel';
import Text from '../Text/Text';
import Icon from '../Icon/Icon';
import { Row } from '../Grid/Grid';
import styles from './styles.css';
import { TopSpaced } from '../Spaced/Spaced';
import { LogoAppBottomBorder } from '../AppCommons/AppBottomBorder';
import Button from '../Button/Button';
const SuccessPanel = ({ title, subtitle }) => {
    const [show, setShow] = useState(true);
    return show ? (<SuccessPagePanel data-testid="successPanel">
      <Row justifyContent="space-between">
        <Text type="title">{title}</Text>
        <Button type="icon" onClick={() => setShow(false)} data-testid="successPanelCloseIcon">
          <Icon name="close" className={styles.closeIcon}/>
        </Button>
      </Row>
      <TopSpaced>{subtitle}</TopSpaced>
      <LogoAppBottomBorder />
    </SuccessPagePanel>) : null;
};
export default SuccessPanel;
