import { EmailTitle, SMSTitle } from '../../../resources/html/otp.html';
class OTPStateTitle {
}
OTPStateTitle.applySMSTitle = (phone) => {
    const smsTitle = $(SMSTitle);
    smsTitle.children('#otpPhone').append(phone);
    $('#OTPTitleContainer').empty().append(smsTitle);
};
OTPStateTitle.applyEmailTitle = (email) => {
    const emailTitle = $(EmailTitle);
    emailTitle.children('#otpEmail').text(email);
    $('#OTPTitleContainer').empty().append(emailTitle);
};
export default OTPStateTitle;
