import ABTestSubject, { ABTestTypes } from '../../services/abTestSubject';
import { invisibleOptInA, invisibleOptInB, } from '../../resources/html/optin/invisibleOptin.html';
import Legal from '../../utils/legal.utils';
import EventService from '../../services/event';
class InvisibleOptIn extends ABTestSubject {
    constructor() {
        super(...arguments);
        this.testName = ABTestTypes.INVISIBLE_OPT_IN;
        this.renderControl = () => {
            this.renderInvisibleOptIn(invisibleOptInA);
        };
        this.renderVariant = () => {
            this.renderInvisibleOptIn(invisibleOptInB);
        };
        this.renderInvisibleOptIn = (component) => {
            const invisibleOptIn = $(component);
            invisibleOptIn.hide();
            $("input[id='contact.email']").parent().append(invisibleOptIn);
            this.setupLegalText();
            invisibleOptIn.slideDown('normal');
            $('#buyMoreFaster').empty();
        };
        this.setupLegalText = () => {
            const legalText = $('#legalTextContainer');
            legalText.hide();
            $('#toggleLegalTextIcon').on('click', () => {
                if (!legalText.is('visible')) {
                    this.eventService.registerInLoggly(EventService.logglyEvents.INVISIBLE_OPTIN_LEGAL_SHOWN);
                }
                legalText.slideToggle();
            });
            $('#privacyPolicyLink').on('click', Legal.openPrivacyPolicy);
        };
    }
}
InvisibleOptIn.TAG = 'invisible-opt-in';
export default InvisibleOptIn;
