/* eslint-disable @typescript-eslint/no-explicit-any */
import Checkout from './checkout';
// eslint-disable-next-line import/prefer-default-export
export function listenTo(events) {
    return function eventHandlerClassDecorator(Base) {
        class Mixin extends Base {
            constructor(...args) {
                super(...args);
                events.forEach((event) => Checkout.subscribeToEvent(event, this.handle.bind(this)));
            }
        }
        return Mixin;
    };
}
