import React from 'react';
import styles from './styles.css';
export const TopSpaced = ({ children, id, size = 'small' }) => (<div id={id} className={styles[`${size}Top`]}>
    {children}
  </div>);
export const RightSpaced = ({ children, id, size = 'small' }) => (<div id={id} className={styles[`${size}Right`]}>
    {children}
  </div>);
export const VerticalSpaced = ({ children, id, size = 'small', }) => (<div id={id} className={styles[`${size}Vertical`]}>
    {children}
  </div>);
