import OtpFlowState from './otpFlowState';
import { longResend, resendViaEmailOrSmsButton, } from '../../../../resources/html/otp.html';
import WebOTP from '../../../../services/webotp';
import OTPStateTitle from '../title';
import OTPConfig from '../config';
import OTPStateHeader from '../header';
/* State after sending via email */
class OtpAfterEmailState extends OtpFlowState {
    constructor() {
        super(...arguments);
        this.resendText = longResend;
        this.resendButton = resendViaEmailOrSmsButton;
        this.sendType = WebOTP.OTPSendTypes.EMAIL;
        this.applyTitle = () => {
            OTPStateTitle.applyEmailTitle(this.data.email);
        };
        this.applyHeader = () => {
            OTPStateHeader.applyEmailIconHeader();
            OTPStateHeader.applyEmailAndSMSTextHeader(this.data);
        };
        this.getTimerConfig = () => ({
            seconds: OTPConfig.LONG_RESEND_WAIT_SECONDS,
        });
        this.setupResend = () => {
            this.otpResend.applyMixedResend(this.data);
        };
    }
}
export default OtpAfterEmailState;
