import optInHtml from '../../../resources/html/optin/optIn.html';
import Legal from '../../../utils/legal.utils';
import addInputPhoneWallet from '../phoneNumber';
import Config from '../../../utils/config.utils';
const addExpandedOptIn = () => {
    const handleAcceptWalletChange = () => {
        $('#phoneWallet').trigger('focus');
    };
    $('#boxOptInWallet').remove();
    const optIn = $(optInHtml);
    $('#optInNext').append(optIn);
    $('#isAcceptWallet').prop('checked', Config.ENABLE_CHECKED_OPT_IN);
    addInputPhoneWallet($('#inputPhoneNumberWallet'));
    $('#termsOfUse').on('click', Legal.openTermsOfUse);
    $('#privacyPolicy').on('click', Legal.openPrivacyPolicy);
    optIn.on('change', '#isAcceptWallet', handleAcceptWalletChange);
};
export default addExpandedOptIn;
