import MessageHandler from '../../messageHandler';
import WalletBeta from '../walletBeta';
import { getCurrentPage, redirectTo } from '../../../utils/window.utils';
import Config from '../../../utils/config.utils';
import Storefront from '../../storefront';
class LoginSuccessMessageHandler extends MessageHandler {
    constructor() {
        super(...arguments);
        this.handleMessage = () => {
            const storeId = Storefront.getStoreId();
            const cartId = Storefront.getCartId();
            const params = {
                external_id: cartId,
                external_store_id: storeId,
                products_info_url: `${Config.NUVEMSHOP_HOST}/${storeId}/orders/${cartId}`,
                return_url: getCurrentPage(),
                store_logo: Storefront.getStoreLogoUrl(),
                store_name: Storefront.getStoreName(),
                store_url: Storefront.getStoreUrl(),
            };
            redirectTo(`${Config.WALLET_IFRAME_HOST}/initialize?${$.param(params)}`);
        };
        this.shouldHandle = (message) => message === WalletBeta.messages.LOGIN_SUCCESS;
    }
}
export default LoginSuccessMessageHandler;
