import { EmailChannelIcon, SMSChannelIcon, } from '../../../resources/html/otp.html';
class OTPStateHeader {
}
OTPStateHeader.applySMSIconHeader = () => {
    const smsIcon = $(SMSChannelIcon);
    $('#OTPChannelIconContainer').empty().append(smsIcon);
};
OTPStateHeader.applyEmailIconHeader = () => {
    const emailIcon = $(EmailChannelIcon);
    $('#OTPChannelIconContainer').empty().append(emailIcon);
};
OTPStateHeader.applyEmailAndSMSTextHeader = (data) => {
    $('#otpEmail').empty().append(data.email);
    $('#otpPhone').empty().append(data.telephone);
};
OTPStateHeader.applyEmailTextHeader = (data) => {
    $('#otpEmail').empty().append(data.email);
    $('#otpPhone').empty();
};
export default OTPStateHeader;
