import HttpClient from './httpClient';
import EventService from './event';
import Storefront from './storefront';
export const BASE_URL = process.env.WALLET_HOST;
export const errorCodes = {
    MAX_PHONE_REACHED: 'MAX_PHONE_REACHED',
};
class WalletApi {
    constructor() {
        this.client = new HttpClient();
        this.createUser = (data) => this.client.post(`${BASE_URL}/users`, data, 'Error creating user');
        this.verifyUserExist = async (email) => {
            const params = {
                email,
                ...EventService.getCurrentEventData(),
            };
            return this.client.get(`${BASE_URL}/users/registered?${$.param(params)}`, 'Error verifying if user exist');
        };
        this.sendOTP = async (via, email) => this.client.post(`${BASE_URL}/auth/send-otp?${$.param({ channel: via })}`, {
            email,
            country: Storefront.getCountry(),
            storeName: Storefront.getStoreName(),
            ...EventService.getCurrentEventData(),
        }, 'Error occurred sending otp');
        this.authToken = (otp, email, channel) => this.client.post(`${BASE_URL}/auth/login`, {
            email,
            otp,
            customer_data: true,
            channel,
            ...EventService.getCurrentEventData(),
        }, 'Error occurred trying to authenticate otp');
        this.registerEvent = (eventData, eventName) => this.client.post(`${BASE_URL}/events`, {
            event: eventName,
            storeId: eventData.store_id,
            eventData,
        }, `Error occurred trying to register event: ${eventName}.`);
        this.registerLogglyEvent = (logText) => {
            this.client.post(`${BASE_URL}/logger`, {
                log: logText,
            }, `Error occurred trying to register event: ${logText}`);
        };
        this.registerEventPersisted = (eventData) => {
            this.client.post(`${BASE_URL}/events/persisted`, {
                eventData,
            });
        };
        this.getAddressForUser = async (data) => {
            const url = Object.keys(data).length > 0
                ? `${BASE_URL}/address?${$.param(data)}`
                : `${BASE_URL}/address`;
            const response = await this.client.get(url);
            if (response.shipping.length === 0) {
                return [];
            }
            return [response.shipping[0], response.billing[0]];
        };
        this.updateAddressForUser = async (data) => {
            await this.client.post(`${BASE_URL}/address`, data, `Error when trying to create address for user: ${JSON.stringify(data)}`);
        };
        this.sendCES = (data) => this.client.post(`${BASE_URL}/ces`, data, `Error occurred trying to send ces data: ${data}`);
        this.createOrder = (data) => this.client.post(`${BASE_URL}/orders`, {
            cart_hash: Storefront.getCartHash(),
            external_cart_id: Storefront.getCartId().toString(),
            external_store_id: Storefront.getStoreId().toString(),
            card_id: data.cardId,
            is_one_tap: data.isOneTap,
            user_id: data.userId,
            created_in: data.createdIn,
        }, 'Error occurred trying to create order');
        this.shouldShowOneTapSurvey = async (externalCartId) => {
            const response = await this.client.get(`${BASE_URL}/orders/show-survey?${$.param({
                external_cart_id: externalCartId,
            })}`);
            return response.show_survey;
        };
        this.getUserWithCookie = async (email) => {
            if (email) {
                return this.client.get(`${BASE_URL}/auth/user?email=${email}`);
            }
            return this.client.get(`${BASE_URL}/auth/user`);
        };
        this.logoutUser = () => this.client.post(`${BASE_URL}/auth/logout`, {
            externalCartId: Storefront.getCartId(),
        });
        this.refreshToken = () => this.client.post(`${BASE_URL}/auth/refresh`, {});
    }
    addAuthorization(accessToken) {
        this.client.addAuthorization(accessToken);
    }
    updateUser(data) {
        return this.client.patch(`${BASE_URL}/users`, data);
    }
}
export default WalletApi;
