import ErrorHandler from './errorHandler';
import { getCurrentStep } from '../utils/window.utils';
class HttpClient {
    constructor() {
        this.TOKEN_TYPE = 'Bearer';
        this.headers = {};
        this.get = (url, errorMessage, settings) => $.ajax({
            method: 'GET',
            url,
            ...this.commonSettings(errorMessage, settings),
        });
        this.post = (url, data, errorMessage, settings) => $.ajax({
            method: 'POST',
            url,
            data: JSON.stringify(data),
            ...this.commonSettings(errorMessage, settings),
            contentType: 'application/json',
        });
        this.patch = (url, data, errorMessage, settings) => $.ajax({
            method: 'PATCH',
            url,
            data: JSON.stringify(data),
            ...this.commonSettings(errorMessage, settings),
            contentType: 'application/json',
        });
        this.addAuthorization = (token) => {
            this.headers.Authorization = `${this.TOKEN_TYPE} ${token}`;
        };
        this.commonSettings = (errorMessage, settings) => ({
            headers: this.headers,
            xhrFields: {
                withCredentials: true,
            },
            error(jqXHR, textStatus, errorThrown) {
                ErrorHandler.reportError(`${errorMessage || 'Unexpected API error'} at ${getCurrentStep()}`, {
                    response: jqXHR.responseJSON,
                    errorThrown,
                    status: jqXHR.status,
                });
                return false;
            },
            ...settings,
        });
    }
}
export default HttpClient;
