import Timer from '../utils/timer.utils';
import { isElementVisible } from '../utils/window.utils';
import ErrorHandler from './errorHandler';
class Monitor {
}
Monitor.reportIfComponentStillPresentAfterSeconds = (componentSelector, seconds, message, callback) => {
    Timer.startWith({
        seconds,
        onTimerFinished: () => {
            if (isElementVisible($(componentSelector))) {
                ErrorHandler.reportError(message);
                if (callback)
                    callback();
            }
        },
    });
};
export default Monitor;
