import WalletApi from './api';
import WebOTP from './webotp';
import EventService from './event';
import LoginService from './login';
import EmailInputHandler from './emailInputHandler';
import Checkout, { CheckoutEvents } from './checkout';
import { doWhenReady, isOnStep } from '../utils/window.utils';
import PersistedSessionHandler from './checkoutHandlers/persistedSessionHandler';
import DeliveryOptInRenderer from './checkoutHandlers/deliveryOptInRenderer';
import LogoutRenderer from './checkoutHandlers/logoutRenderer';
import SuccessOptInRenderer from './checkoutHandlers/successOptInRenderer';
import UserCreator from './checkoutHandlers/userCreator';
import AfterCloseOrderDataSetter from './checkoutHandlers/afterCloseOrderDataSetter';
import SuccessPageRenderer from './checkoutHandlers/successPageRenderer';
import ZipcodeButtonHandler from './checkoutHandlers/zipcodeButtonHandler';
import LogoutHandler from './checkoutHandlers/logoutHandler';
import WalletBootstrap from './walletBootstrap';
import UserUpdater from './checkoutHandlers/userUpdater';
import StartPageRenderer from './checkoutHandlers/startPageRenderer';
import OrderCreator from './checkoutHandlers/orderCreator';
import SurveyRenderer from './checkoutHandlers/surveyRenderer';
import PersistedAuthProxyHandler from './checkoutHandlers/persistedAuthProxyHandler';
import AddressCreator from './checkoutHandlers/addressCreator';
class CheckoutHandler {
    constructor() {
        this.eventHandlersToCallOnRefresh = {
            start: ['startPageRenderer'],
            success: ['successPageRenderer', 'surveyRenderer'],
        };
        this.start = () => {
            this.setupEventHandlersToBeCalledOnRefresh();
            new WalletBootstrap(this.appProps.emailInputHandler, this.webOTP, this.appProps).start();
            Checkout.publishEvent(CheckoutEvents.IS_READY, true);
        };
        this.setupEventHandlersToBeCalledOnRefresh = () => {
            Object.keys(this.eventHandlersToCallOnRefresh).forEach((step) => {
                if (isOnStep(step)) {
                    doWhenReady(() => {
                        this.eventHandlersToCallOnRefresh[step].forEach((eventHandler) => {
                            this.eventHandlers[eventHandler].handle(CheckoutEvents.REDIRECT_USER, step);
                        });
                    });
                }
            });
        };
        const api = new WalletApi();
        this.webOTP = new WebOTP(api);
        const eventService = new EventService(this.webOTP, api);
        const loginService = new LoginService(api, eventService);
        const emailInputHandler = new EmailInputHandler(api);
        this.appProps = {
            api,
            eventService,
            loginService,
            beforeCloseOrderData: null,
            afterCloseOrderData: null,
            emailInputHandler,
            webOTP: this.webOTP,
        };
        this.eventHandlers = {
            startPageRenderer: new StartPageRenderer(this.appProps),
            successPageRenderer: new SuccessPageRenderer(this.appProps),
            persistedSessionHandler: new PersistedSessionHandler(this.appProps),
            deliveryOptInRenderer: new DeliveryOptInRenderer(this.appProps),
            logoutRenderer: new LogoutRenderer(this.appProps),
            successOptInRenderer: new SuccessOptInRenderer(this.appProps),
            userCreator: new UserCreator(this.appProps),
            afterCloseOrderDataSetter: new AfterCloseOrderDataSetter(this.appProps),
            zipcodeButtonHandler: new ZipcodeButtonHandler(this.appProps),
            logoutHandler: new LogoutHandler(this.appProps),
            userUpdater: new UserUpdater(this.appProps),
            orderCreator: new OrderCreator(this.appProps),
            surveyRenderer: new SurveyRenderer(this.appProps),
            persistedAuthProxyHandler: new PersistedAuthProxyHandler(this.appProps),
            addressCreator: new AddressCreator(this.appProps),
        };
    }
}
export default CheckoutHandler;
