import modalHtml from '../resources/html/modal.html';
export const closeModal = () => $('#modalAppWallet').remove();
// eslint-disable-next-line import/prefer-default-export
export const openModal = (title, content) => {
    const modal = $(modalHtml);
    $('body')
        .append(modal)
        .on('click', '#modalAppWallet .modal-backdrop', closeModal)
        .on('click', '#modalAppWallet .modal-close', closeModal)
        .on('click', '#modalOkButton', closeModal);
    $('.modal-title').append(title);
    $('.content-modal').prepend(content);
};
