import Storefront from './storefront';
class AddressService {
    constructor(api) {
        this.addressForUser = () => this.api.getAddressForUser(Storefront.getAddressIdentifiers());
        this.updateAddress = (data) => this.api.updateAddressForUser(data);
        this.storefrontAddressForCheckout = (user) => {
            const storefrontAddress = Storefront.getAddressBy(user);
            return [storefrontAddress, { ...storefrontAddress, type: 'billing' }];
        };
        this.api = api;
    }
}
AddressService.requiredFields = [
    'type',
    'zipcode',
    'address',
    'number',
    'city',
    'state',
    'country',
];
AddressService.isNull = (address) => AddressService.requiredFields.some((field) => !address[field]);
export default AddressService;
