import Storefront from '../../storefront';
import Checkout from '../../checkout';
import MessageHandler from '../../messageHandler';
import WalletIframe from '../walletIframe';
import WebOTP from '../../webotp';
class ReadyMessageHandler extends MessageHandler {
    constructor() {
        super(...arguments);
        this.FADE_OUT_DURATION_MS = 500;
        this.handleMessage = () => {
            console.log('[wallet-auth-proxy] WalletIframe: ReadyMessageHandler');
            $('#walletSpinner').fadeOut(this.FADE_OUT_DURATION_MS, () => {
                $('#walletIframe').css('visibility', 'visible');
                $('#iframeSpinner').remove();
            });
            WalletIframe.postMessage(WalletIframe.messages.INITIALIZE, {
                cartId: Storefront.getCartId(),
                cartHash: Storefront.getCartHash(),
                storeId: Storefront.getStoreId(),
                email: Checkout.getEmail(),
                channel: WebOTP.OTPSendTypes.SMS,
            });
        };
        this.shouldHandle = (message) => message === WalletIframe.messages.READY;
    }
}
export default ReadyMessageHandler;
