import UserService from '../../services/user';
import addInputPhoneWallet, { getFullPhoneNumber, getInitialFormattedPhone, hideOptinForm, showOptinForm, validatePhone, } from './phoneNumber';
import ErrorHandler from '../../services/errorHandler';
import { boxOptInSuccessHtml, succesMsgContent, } from '../../resources/html/optin/boxOptInSuccess.html';
import Legal from '../../utils/legal.utils';
import { NameCookiesUsed, removeCookie } from '../../utils/cookie.utils';
import OrderService from '../../services/order';
const hideInfoPhoneCurrent = () => {
    $('*[data-ctrlInf]').addClass('d-none');
};
const successToCreatedUser = () => {
    $('#titleStatusContent').css({ 'padding-bottom': '6px' });
    $('#titleStatusContent span:first-child')
        .text('Dados salvos com segurana!')
        .append(succesMsgContent);
    $('#bodyStatusContent').addClass('d-none');
    $('#closeBoxOptInSuccess').addClass('d-inline-block');
};
const loadingButtonOptin = (showLoading) => {
    $('#textBtnOptinInSuccess').css({ display: showLoading ? 'none' : 'flex' });
    $('#loadingBtnOptinInSuccess').css({
        display: showLoading ? 'block' : 'none',
    });
};
const errorToCreateUser = (error) => {
    loadingButtonOptin(false);
    $('#errorRequestSaveSuccess').css({ display: 'block' });
    ErrorHandler.reportException(error);
};
const showFormHideLabel = () => {
    hideInfoPhoneCurrent();
    showOptinForm();
};
const addOptInOnSuccess = (data, appProps) => {
    $('#boxOptInSuccess').remove();
    $('#titleSaveInfoNextTime').html('Acompanhe novidades do seu pedido');
    const element = boxOptInSuccessHtml();
    $('#optInSuccess').append(element);
    addInputPhoneWallet($('#inputPhoneNumberWallet'));
    if (getInitialFormattedPhone()) {
        hideOptinForm();
    }
    else {
        showFormHideLabel();
    }
    $('#termsOfUse').on('click', Legal.openTermsOfUse);
    $('#privacyPolicy').on('click', Legal.openPrivacyPolicy);
    $('#changePhoneNumber').on('click', () => {
        showFormHideLabel();
    });
    $('#btnOptInSuccess').on('click', async () => {
        const userService = new UserService({
            ...data,
            phone: getFullPhoneNumber(),
            with_consent: true,
            consented_in: 'opt-in-tracking-page',
        }, appProps.api);
        try {
            const isValidPhoneForWallet = validatePhone();
            if (isValidPhoneForWallet) {
                loadingButtonOptin(true);
                const createdUser = await userService.create();
                const createOrderData = {
                    isOneTap: false,
                    userId: createdUser === null || createdUser === void 0 ? void 0 : createdUser.id,
                    createdIn: 'opt-in-tracking-page',
                };
                await new OrderService(appProps, createOrderData).create();
                successToCreatedUser();
                userService.setCookieEmail();
            }
        }
        catch (err) {
            removeCookie(NameCookiesUsed.CHECKOUT_SAVE_COOKIE_EMAIL);
            errorToCreateUser(err);
        }
    });
    $('#closeBoxOptInSuccess').on('click', () => {
        $('#boxOptInSuccess').addClass('d-none');
    });
};
export default addOptInOnSuccess;
