import React from 'react';
import styles from './styles.css';
export function Column({ children }) {
    return <div className={styles.column}>{children}</div>;
}
export function Row({ children, justifyContent = 'center', alignItems = 'center', }) {
    return (<div className={styles.row} style={{ justifyContent, alignItems }}>
      {children}
    </div>);
}
