import Storefront from './storefront';
export const ABTestTypes = {
    OPT_IN: 'ab-cart-wallet-optIn',
    INVISIBLE_OPT_IN: 'ab-cart-invisible-opt-in',
};
export const ABClass = {
    CONTROL: 'a',
    VARIANT: 'b',
};
class ABTestSubject {
    constructor(eventService) {
        this.eventService = eventService;
        this.render = () => {
            if (this.isVariant()) {
                this.renderVariant();
            }
            else {
                this.renderControl();
            }
        };
        this.registerTestResult = () => {
            const currentTestClass = this.current();
            if (currentTestClass) {
                this.eventService.registerABTest(this.testName, currentTestClass);
            }
        };
        this.isVariant = () => this.current() === ABClass.VARIANT;
        this.current = () => (Storefront.getActiveTestAB() || {})[this.testName];
    }
}
export default ABTestSubject;
