class UserTestService {
}
// Currently, using hotjar
// https://help.hotjar.com/hc/en-us/articles/360034216634-Hotjar-JavaScript-Functions-Reference
UserTestService.Hotjar = window.hj;
UserTestService.start = () => {
    if (UserTestService.Hotjar) {
        UserTestService.Hotjar('event', ['wallet_customer_identification']);
    }
};
export default UserTestService;
