import { closeModal } from '../../../utils/modal.utils';
import WalletAuthProxy from '../walletAuthProxy';
import MessageHandler from '../../messageHandler';
import { convertCamelToSnake } from '../../../utils/string.utils';
class LoginSuccessMessageHandler extends MessageHandler {
    constructor(appProps) {
        super();
        this.handleMessage = (payload) => {
            console.log('[wallet-auth-proxy] WalletAuthProxy: LoginSuccessMessageHandler');
            if (payload) {
                closeModal();
                const camelCaseUserObj = convertCamelToSnake(payload.user);
                this.appProps.loginService.login({
                    ...payload,
                    user: camelCaseUserObj,
                });
            }
        };
        this.shouldHandle = (message) => message === WalletAuthProxy.messages.LOGIN_SUCCESS;
        this.appProps = appProps;
    }
}
export default LoginSuccessMessageHandler;
