import MessageHandler from '../../messageHandler';
import WalletBeta from '../walletBeta';
import { showError } from '../../../features/login/modal';
class LoginFailureMessageHandler extends MessageHandler {
    constructor() {
        super(...arguments);
        this.handleMessage = () => {
            showError();
        };
        this.shouldHandle = (message) => message === WalletBeta.messages.LOGIN_FAILURE;
    }
}
export default LoginFailureMessageHandler;
