import OtpFlowState from './otpFlowState';
import { genericResend, longResend } from '../../../../resources/html/otp.html';
import WebOTP from '../../../../services/webotp';
import OTPStateTitle from '../title';
import OTPStateHeader from '../header';
import OTPConfig from '../config';
class OtpEmailState extends OtpFlowState {
    constructor() {
        super(...arguments);
        this.resendText = longResend;
        this.resendButton = genericResend;
        this.sendType = WebOTP.OTPSendTypes.EMAIL;
        this.shouldShowTermsAndConditions = false;
        this.applyTitle = () => {
            OTPStateTitle.applyEmailTitle(this.data.email);
        };
        this.applyHeader = () => {
            OTPStateHeader.applyEmailIconHeader();
            OTPStateHeader.applyEmailTextHeader(this.data);
        };
        this.getTimerConfig = () => ({
            seconds: OTPConfig.SHORT_RESEND_WAIT_SECONDS,
        });
        this.setupResend = () => {
            this.otpResend.applyGenericResendViaEmail(this.data);
        };
    }
}
export default OtpEmailState;
