import Storefront from './storefront';
import Checkout from './checkout';
import Config from '../utils/config.utils';
import { getCurrentPage } from '../utils/window.utils';
class EventService {
    constructor(webOTP, api) {
        this.inputManual = true;
        this.eventDataForRequest = () => ({
            store_id: EventService.getCurrentEventData().storeId,
            cart_id: EventService.getCurrentEventData().cartId,
            cart_hash: EventService.getCurrentEventData().cartHash,
            email: Checkout.getEmail(),
            timestamp: new Date().toISOString(),
        });
        this.registerEventPersisted = (eventData) => {
            this.api.registerEventPersisted({
                ...this.eventDataForRequest(),
                ...eventData,
            });
        };
        this.api = api;
        webOTP.subscribe(() => {
            this.inputManual = false;
        });
    }
    registerCustomIdentification(otherProps) {
        const storeId = Storefront.getStoreId();
        const cartId = Storefront.getCartId();
        return this.api.registerEvent({
            products_info_url: `${Config.NUVEMSHOP_HOST}/${storeId}/orders/${cartId}`,
            return_url: getCurrentPage(),
            store_logo: Storefront.getStoreLogoUrl(),
            store_name: Storefront.getStoreName(),
            store_url: Storefront.getStoreUrl(),
            ...this.eventDataForRequest(),
            ...otherProps,
        }, EventService.events.CUSTOMER_IDENTIFICATION);
    }
    registerOTPReceived(otherProps) {
        this.api.registerEvent({
            ...this.eventDataForRequest(),
            input_manual: this.inputManual,
            ...otherProps,
        }, EventService.events.OTP_RECEIVED);
    }
    registerOTPDismissed() {
        this.api.registerEvent(this.eventDataForRequest(), EventService.events.OTP_DISMISSED);
    }
    registerABTest(testName, current) {
        this.api.registerEvent({
            ...this.eventDataForRequest(),
            testName,
            current,
        }, EventService.events.AB_TEST);
    }
    registerOTPClosedImmediately() {
        this.api.registerEvent(this.eventDataForRequest(), EventService.events.OTP_CLOSED_IMMEDIATELY);
    }
    registerInLoggly(eventName) {
        this.api.registerLogglyEvent(eventName);
    }
}
EventService.events = {
    AB_TEST: 'wallet_ab_test',
    CUSTOMER_IDENTIFICATION: 'wallet_customer_identification',
    OTP_CLOSED_IMMEDIATELY: 'wallet_otp_closed_immediately',
    OTP_DISMISSED: 'wallet_otp_dismissed',
    OTP_RECEIVED: 'wallet_otp_received',
};
EventService.logglyEvents = {
    INVISIBLE_OPTIN_RENDERED: 'wallet_invisible_optin_rendered',
    INVISIBLE_OPTIN_CLICKED: 'wallet_invisible_optin_clicked',
    INVISIBLE_OPTIN_OTP_SEND: 'wallet_invisible_optin_otp_send',
    INVISIBLE_OPTIN_LOGIN: 'wallet_invisible_optin_login',
    INVISIBLE_OPTIN_LEGAL_SHOWN: 'wallet_invisible_optin_legal_show',
};
EventService.getCurrentEventData = () => ({
    cartHash: Storefront.getCartHash(),
    storeId: Storefront.getStoreId(),
    cartId: Storefront.getCartId(),
});
export default EventService;
