import Cookies from 'js-cookie';
export const ConfigCookies = {
    SESSION_DURATION_AUTHENTICATED: 90 * 24 * 60 * 60 * 1000,
    SESSION_DURATION_ACCESS_TOKEN: 120 * 60 * 1000,
};
const nowInMilliseconds = new Date().getTime();
export const NameCookiesUsed = {
    CHECKOUT_SAVE_COOKIE_EMAIL: 'checkout-save-cookie-email',
    WALLET_AUTHENTICATED: 'wallet-authenticated',
    WALLET_ACCESS_TOKEN: 'wallet-access-token',
    WALLET_SAME_STORE_AUTHENTICATED: 'wallet-same-store-authenticated',
    WALLET_CROSS_STORE_IMPOSSIBLE: 'wallet-cross-store-impossible',
};
export const SameStoreType = {
    SAME_STORE: 1,
    CROSS_STORE: 2,
};
export const getCookieByKeyName = (nameCookie) => Cookies.get(nameCookie);
export const setSameStore = () => {
    Cookies.set(NameCookiesUsed.WALLET_SAME_STORE_AUTHENTICATED, String(SameStoreType.SAME_STORE));
};
export const setCrossStore = () => {
    Cookies.set(NameCookiesUsed.WALLET_SAME_STORE_AUTHENTICATED, String(SameStoreType.CROSS_STORE));
};
export const setCrossStoreImpossible = (store) => {
    Cookies.set(NameCookiesUsed.WALLET_CROSS_STORE_IMPOSSIBLE, store ? 'true' : 'false');
};
export const getCrossStoreImpossible = () => {
    const crossStoreImpossible = Cookies.get(NameCookiesUsed.WALLET_CROSS_STORE_IMPOSSIBLE);
    if (!crossStoreImpossible) {
        return false;
    }
    return crossStoreImpossible === 'true';
};
export const setCheckoutSaveCookieEmail = (email) => {
    if (!email)
        return;
    Cookies.set(NameCookiesUsed.CHECKOUT_SAVE_COOKIE_EMAIL, email);
};
export const removeCookie = (nameCookie) => Cookies.remove(nameCookie);
export const verifyCookieAuthenticated = () => getCookieByKeyName(NameCookiesUsed.WALLET_AUTHENTICATED);
export const removeCookiesAuth = () => {
    Cookies.remove(NameCookiesUsed.WALLET_AUTHENTICATED);
    Cookies.remove(NameCookiesUsed.WALLET_ACCESS_TOKEN);
};
export const setCookieWalletAuthenticated = () => {
    const expiresAuthenticated = new Date(nowInMilliseconds + ConfigCookies.SESSION_DURATION_AUTHENTICATED);
    Cookies.set(NameCookiesUsed.WALLET_AUTHENTICATED, 'authenticated', {
        expires: expiresAuthenticated,
    });
};
export const setSessionCookieWalletAuthenticated = () => {
    Cookies.set(NameCookiesUsed.WALLET_AUTHENTICATED, 'authenticated');
};
export const seCookieWalletAccessToken = (accessToken) => {
    const expiresAccessToken = new Date(nowInMilliseconds + ConfigCookies.SESSION_DURATION_ACCESS_TOKEN); // 120 min
    Cookies.set(NameCookiesUsed.WALLET_ACCESS_TOKEN, accessToken, {
        expires: expiresAccessToken,
    });
};
