import InvisibleOptInModalOpener from '../features/login/modalOpen/invisibleOptInModalOpener';
import OTPModal from '../features/login/otp';
import Checkout from './checkout';
import Storefront from './storefront';
import InvisibleOptIn from '../features/optin/invisibleOptIn';
import LoginService from './login';
import EventService from './event';
import InvisibleOptInOTPFlow from '../features/login/otpFlow/otpFlows/invisibleOptInOtpFlow';
class WalletBootstrap {
    constructor(emailInputHandler, webOTP, appProps) {
        this.emailInputHandler = emailInputHandler;
        this.webOTP = webOTP;
        this.appProps = appProps;
        this.modalOpener = null;
        this.handleUserWithConsent = (userRegisteredResult, timer, newEmail) => {
            if (newEmail) {
                this.otpModal.setupForUserWithConsent(userRegisteredResult);
            }
            this.otpModal.show(userRegisteredResult, newEmail);
            this.appProps.loginService.setLoginType(LoginService.loginTypes.DEFAULT);
        };
        this.handleUserWithoutConsent = (userRegisteredResult, timer, newEmail) => {
            if (Storefront.hasTag(InvisibleOptIn.TAG)) {
                this.enableInvisibleOptInFlow(userRegisteredResult, timer, newEmail);
            }
            else {
                Checkout.enableSmartSignInFlow(userRegisteredResult.email);
            }
        };
        this.handleNonExistingUser = (userRegisteredResult) => {
            this.otpModal.hideReOpener();
            Checkout.enableSmartSignInFlow(userRegisteredResult.email);
        };
        this.handleEmailPrefilled = (userRegisteredResult) => {
            const userIsLoggedInStore = Checkout.userIsLoggedInStore();
            const userIsLoggedInWallet = this.appProps.loginService.isUserLogged();
            if (!userIsLoggedInStore && !userIsLoggedInWallet) {
                this.otpModal.setupForUserWithConsent(userRegisteredResult);
                let shouldStartNewFlow = true;
                const handleClick = () => {
                    this.otpModal.show(userRegisteredResult, shouldStartNewFlow);
                    shouldStartNewFlow = false;
                };
                this.otpModal.showReOpenerIfNotPresent(handleClick);
            }
        };
        this.enableInvisibleOptInFlow = (userRegisteredResult, timer, newEmail) => {
            let shouldStartNewFlow = newEmail;
            const handleInvisibleOptInClick = () => {
                this.otpModal.show(userRegisteredResult, shouldStartNewFlow);
                shouldStartNewFlow = false;
                this.appProps.eventService.registerInLoggly(EventService.logglyEvents.INVISIBLE_OPTIN_CLICKED);
            };
            this.modalOpener = new InvisibleOptInModalOpener();
            this.otpModal.setReOpener(this.modalOpener);
            this.otpModal.changeFlowTo(new InvisibleOptInOTPFlow(this.webOTP));
            this.modalOpener.showIfNotPresent(this.appProps, handleInvisibleOptInClick);
            this.appProps.eventService.registerInLoggly(EventService.logglyEvents.INVISIBLE_OPTIN_RENDERED);
            this.appProps.loginService.setLoginType(LoginService.loginTypes.INVISIBLE_OPT_IN);
        };
        this.otpModal = new OTPModal(webOTP, appProps);
    }
    start() {
        this.emailInputHandler.subscribeTo('userWithConsent', this.handleUserWithConsent);
        this.emailInputHandler.subscribeTo('userWithoutConsent', this.handleUserWithoutConsent);
        this.emailInputHandler.subscribeTo('nonExistingUser', this.handleNonExistingUser);
        this.emailInputHandler.subscribeTo('emailPrefilled', this.handleEmailPrefilled);
    }
}
export default WalletBootstrap;
